"use client";

import { useState, useRef, useEffect } from 'react';
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";

interface Message {
  role: 'user' | 'assistant';
  content: string;
  timestamp: number;
}

export default function Home() {
  const [messages, setMessages] = useState<Message[]>([
    {
      role: 'assistant',
      content: 'The flames have guided you here. What visions do you seek?',
      timestamp: Date.now()
    }
  ]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTop = scrollAreaRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim()) return;

    const userMessage: Message = {
      role: 'user',
      content: input,
      timestamp: Date.now()
    };

    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);

    try {
      // Simulate API call to AI processing workflow
      // In production, this would call your actual API endpoint
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const assistantMessage: Message = {
        role: 'assistant',
        content: 'The Lord of Light shows me your path. [This is a placeholder - In production, this would be the actual response from your AI processing workflow]',
        timestamp: Date.now()
      };

      setMessages(prev => [...prev, assistantMessage]);
    } catch (error) {
      console.error('Error:', error);
      const errorMessage: Message = {
        role: 'assistant',
        content: 'The visions are unclear. The flames flicker with uncertainty.',
        timestamp: Date.now()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="container max-w-4xl mx-auto p-4">
      <Card className="border-red-900/20 bg-black/80 backdrop-blur-sm">
        <div className="p-6">
          <div className="mb-4 text-center">
            <h1 className="text-2xl font-bold text-red-600 mb-2">Visions in the Flames</h1>
            <p className="text-gray-400">Seek wisdom through the fires of R'hllor</p>
          </div>
          
          <ScrollArea className="h-[500px] rounded-md border border-red-900/20 p-4" ref={scrollAreaRef}>
            <div className="space-y-4">
              {messages.map((message, index) => (
                <div
                  key={index}
                  className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div
                    className={`max-w-[80%] rounded-lg px-4 py-2 ${
                      message.role === 'user'
                        ? 'bg-red-900/20 text-white'
                        : 'bg-black border border-red-900/40 text-red-500'
                    }`}
                  >
                    <p className="text-sm">{message.content}</p>
                    <time className="text-xs text-gray-500 mt-1">
                      {new Date(message.timestamp).toLocaleTimeString()}
                    </time>
                  </div>
                </div>
              ))}
              {isLoading && (
                <div className="flex justify-start">
                  <div className="max-w-[80%] rounded-lg px-4 py-2 bg-black border border-red-900/40">
                    <div className="flex space-x-2 items-center text-red-500">
                      <div className="w-2 h-2 rounded-full bg-red-500 animate-pulse"></div>
                      <div className="w-2 h-2 rounded-full bg-red-500 animate-pulse delay-75"></div>
                      <div className="w-2 h-2 rounded-full bg-red-500 animate-pulse delay-150"></div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </ScrollArea>

          <form onSubmit={handleSubmit} className="mt-4">
            <div className="flex space-x-2">
              <Input
                type="text"
                placeholder="Share your thoughts with the flames..."
                value={input}
                onChange={(e) => setInput(e.target.value)}
                className="flex-1 bg-black/50 border-red-900/20 text-white placeholder:text-gray-500"
              />
              <Button 
                type="submit" 
                disabled={isLoading}
                className="bg-red-900/80 hover:bg-red-800 text-white"
              >
                {isLoading ? 'Consulting...' : 'Send'}
              </Button>
            </div>
          </form>
        </div>
      </Card>
    </div>
  );
}
